   // 
   formatDate(date){
     var dd = date.getDate();
     if (dd < 10) dd = '0' + dd;

     var mm = date.getMonth() + 1;
     if (mm < 10) mm = '0' + mm;

     var yy = date.getFullYear() % 100;
     if (yy < 10) yy = '0' + yy;

     return dd + '.' + mm + '.' + yy;

   }
//   
   termOfTraining(date){
     if(typeof(date) !== 'string'){
       var d = this.formatDate(date)
     }
     this.setState({
       termOfTraining: d
     })
   }

  //   
    trainingInTheUTC(date){
      if(typeof(date) !== 'string'){
        var d = this.formatDate(date)
      }
      this.setState({
        trainingInTheUTC: d
      })
    }

  //   
    trainingInTheUnit(date){
      if(typeof(date) !== 'string'){
        var d = this.formatDate(date)
      }
      this.setState({
        trainingInTheUnit: d
      })
    }

  //  
    registrationDateOfTheProgram(date){
      if(typeof(date) !== 'string'){
        var d = this.formatDate(date)
      }
      this.setState({
        registrationDateOfTheProgram: d
      })
    }
  //  
    dateOfDelivery(date){
      if(typeof(date) !== 'string'){
        var d = this.formatDate(date)
      }
      this.setState({
        dateOfDelivery: d
      })
    }




