import React, { Component } from 'react';
import { connect } from 'react-redux';
import edit from '../img/edit.png';

class Table extends Component {
   render(){
    
      var keys = new Date().getTime();
      return(
         <table>
            <tbody>
               <tr>
                  <td className="cap"></td>
                  <td className="cap"> </td>
                  <td className="cap">...</td>
                  <td className="cap"></td>
                  <td className="cap"> </td>
                  <td className="cap"></td>
                  <td className="cap"></td>
                  <td className="cap"> </td>
                  <td className="cap">  </td>
                  <td className="cap">  </td>
                  <td className="cap">  </td>
                  <td className="cap">  </td>
                  <td className="cap">  </td>
                  <td className="cap">...</td>
                  <td className="cap"></td>
               </tr>
               {this.props.data.allDocuments.map((doc, index) =>
                  <tr key={index+(1*keys)}>
                     <td><img src={edit} className="iconEdit" alt="" /></td>
                     <td>{doc.programmNumber}</td>
                     <td>{doc.fullName}</td>
                     <td>{doc.company}</td>
                     <td>{doc.personnelCategory}</td>
                     <td>{doc.subdivision}</td>
                     <td>{doc.position}</td>
                     <td>{doc.distributionOfTraining}</td>
                     <td>{doc.termOfTraining}</td>
                     <td>{doc.trainingInTheUTC}</td>
                     <td>{doc.trainingInTheUnit}</td>
                     <td>{doc.registrationDateOfTheProgram}</td>
                     <td>{doc.dateOfDelivery}</td>
                     <td>{doc.fullNameProgram}</td>
                     <td>{doc.link}</td>
                  </tr>
               )}
            </tbody>
         </table>


      );
   }
}

export default connect(
   state => ({
      data: state
   }),
   dispatch => ({

   })
)(Table);



